body {
  background: #161616;
  margin: 0;  
  font-family: 'Raleway', sans-serif;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.header{
  background: #161616;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.267);
}
.heightMax{
  height: 100vh;
}
.icon {
  font-size: 36px;
}
.main-box{
  height:500px;
}
.main-bar-box{
  width:150px;
}

.main-bar{
  margin-top: 3em;
  height:5px;
  width:100%;
  background-color: white;
  box-shadow: 0px 0px 6px black;
}
.main-text-lg{
  text-shadow: 0px 3px 6px rgba(0, 0, 0, 0.233);
  width: 680px;
  font-size: 64px;
}
@media (max-width: 700px) {
  .main-box{
    height: 250px;
  }
  .main-text-lg{
    width: 100%;
    font-size: 32px;
  }
  .main-text-sm{
    font-size: 12px;
  }
}
.text-opacity{
  opacity: .6;
}
.main-text-sm{
  font-size: 16px;
  text-shadow: 0px 3px 6px rgba(0, 0, 0, 0.281);
  width: 350px;
}
.main-button{
  width:250px;
  cursor: pointer;
  height:70px;
  border:1px solid rgb(255, 255, 255);
  box-shadow: 0px 0px 20px black;
  transition: .5s all linear;
  border-radius: 80px;
}
.main-button:hover{
  border-color: #BFA75D;
}
.color-3{
  transition: 1s all linear;
  color: white;
}
.color-3:hover{
  color: #BFA75D;
}
.colors{
  color: #d3be80;
  transition: 1s all linear;
  animation: animate 10s linear infinite;
}
.colors-icon{
  color: white;
  cursor: pointer;
  transition: 1s all ease-in;
}
.colors-icon:hover{
  color: #BFA75D;
}

@keyframes animate{
  0%{
    color: #BFA75D;
    text-shadow:0px 0px 10px #BFA75D;
  }
  30%{
    color: #e66852;
    text-shadow:0px 0px 10px #e652e6;

  }
  60%{
    color: #52e6e6;
    text-shadow:0px 0px 10px #52e6e6;

  }
  90%{
    color: #8372cc;
    text-shadow:0px 0px 10px #8372cc;
  }
}