.work-item {
  width: 500px;
  height: 550px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.479);
  border-radius: 15px;
}
@media (max-width: 700px) {
  .work-item{
      width: 100%;
  }
}

::-webkit-scrollbar {
  width: 3px;
}
.work-item img{
    box-shadow: 0px 0px 20px black;
}
.work-title{
    font-size: 24px;
}
/* Track */
::-webkit-scrollbar-track {
  background: #1111113f;
}
.menu div {
  cursor: pointer;
  transition:all .5s linear;
}
.menu div:hover {
  color: #bfa75d;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #bfa75d;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #f1ce62;
}
